






































.mask_transparent {
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.toast{
	position: absolute;
	top:50%;
	left:50%;
	background: rgba(40, 40, 40, 0.75);
	transform:translateX(-50%) translateY(-50%);
	color:#fff;
    text-align: center;
    border-radius:6px;
    padding:10px;
    opacity:1;
    transition:opacity .3s linear;
    z-index: 10000;
}


.toast-leave{
	opacity:0;
}




