







































































.modal-mask {
  position: fixed;
  z-index: 99999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 280px;
  margin: 0px auto;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}
.modal-header{
   padding: 10px 10px 0 10px;
   text-align: center;
}
.modal-header h3 {
  margin-top: 0;
  color: #333;
}

.modal-body {
  padding: 15px 10px;
}
.modal-footer{
	display: -ms-flexbox;
	display: flex;
	position: relative;
}
.modal-footer:before {
	content: " ";
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 1px;
	border-top: 1px solid #d9d9d9;
	color: #d9d9d9;
	transform-origin: 0 0;
	transform: scaleY(.5);
	left: 0;
}
.modal-footer button{
	-ms-flex: 1;
	    flex: 1;
	padding:13px 10px;
}
.modal-default-button {
  float: right;
}
.modal-enter, .modal-leave {
  opacity: 0;
}
.modal-enter .modal-container,
.modal-leave .modal-container {
  transform: scale(1.1);
}




