.loading{z-index:10001;overflow:hidden;position:fixed;top:0;right:0;bottom:0;left:0;width:100%;height:100%;color:#fff}.loading .load-box{position:absolute;top:50%;left:50%;width:100%;transform:translate(-50%, -50%);text-align:center;color:#333;font-size:12px}.spinner{width:60px;height:60px;position:relative;margin:10px auto}.double-bounce1{background-color:#FF6666}.double-bounce2{background-color:#FF6666}.double-bounce1,.double-bounce2{width:100%;height:100%;border-radius:50%;opacity:0.6;position:absolute;top:0;left:0;animation:bounce 2.0s infinite ease-in-out}.double-bounce2{animation-delay:-1.0s}@keyframes bounce{0%,100%{transform:scale(0);-webkit-transform:scale(0)}50%{transform:scale(1);-webkit-transform:scale(1)}}



