.app-nav{z-index:9999;position:fixed;bottom:0;left:0;right:0;width:100%;transform:translateZ(0);background:#fff}.app-nav:before{content:" ";position:absolute;left:0;top:0;width:100%;height:1px;border-top:1px solid #d9d9d9;color:#d9d9d9;transform-origin:0 0;transform:scaleY(0.5)}.app-nav .tab-bar{padding-top:2px;width:100%;height:55px;text-align:center;display:-ms-flexbox;display:flex;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-pack:justify;justify-content:space-between}.app-nav .tab-bar a{color:#5D656B;-ms-flex:1;flex:1;text-align:center;box-sizing:border-box;display:block;font-size:14px}.app-nav .tab-bar a .icon{font-size:18px;margin-bottom:5px;display:block}.custom-active{color:#ff6666 !important}



