body .header-table{background-color:#eee;color:#333}.app-header{position:relative;padding:0 10px;box-sizing:border-box;height:45px;line-height:45px;background:#ff6666;color:#fff}.app-header .h-left,.app-header .h-right{position:absolute;top:0;z-index:100;width:60px;text-align:center}.app-header .h-left{left:0}.app-header .h-right{right:0}.app-header .app-title{position:absolute;top:0;left:0;width:100%;padding:0 60px;text-align:center;font-size:18px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}



